/* On screens that are 600px or less, set the background color to olive */
@media (width <=770px) {
    .slides {
        display: block;

    }

    .project-slide {

        padding: 1rem;
        max-width: 80vw;
        /* width: auto; */
        height: auto;
        margin: 10px auto;
        display: block;

    }

    .project-slide .img {
        margin-top: 18px;
        font-size: 100px;
    }


    /* contact */
    .contact {
        /* width: 80vw; */
        margin-top: 110rem;
        
    }

    .contact-section {
        width: 60vw;
        position: relative;
        /* margin: auto; */
        /* margin-bottom: 10vh; */
    }

    .subhead{
        font-size: x-large;

    }

    .subhead2{
        font-size: large;
    }

    .subhead2 Link, a{
        font-size: small;
    }

    .form-div {
        /* padding: 3rem; */
        width: 70vw;
        margin: auto;
        left: 0;
        margin-bottom: 10vh;
    }

    .form-div button{
        width: 70vw;
    }

    .form-div .subhead{
        /* padding: 12px; */
        font-size: x-large;
    }

    .form-div label{
        font-size: small;
    }

    .form-div input {
        font-size: smaller;
        width: 60vw;
        height: auto;
        text-align: center;
    }

    .contact-slogan{
        /* width: 20vw; */
        margin: 0 22px ;
    }

    .contact a{
        font-size: small;
    }


    /* about */
    .about-me {

        width: auto;
        padding: 1rem 0;
        margin: 1.5rem;
    }

    .desc p {
        margin: 2rem;
    }
}